.img-container {
    float: left;
    width:20%;
    position: relative;
    flex-wrap: nowrap;
 }

 .main{
    display: flex;
    text-align: center;
    border:10px solid white;
    width:136px;
    height:146px;
    margin : 40px auto;
    box-shadow: 0px 0px 25px black;
    overflow: hidden;
    flex-wrap: wrap;
   }
.img1{
    
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 0px;
    width: 150px;
    height: 120px;
    margin: 2px;
    background-size: cover;

}
.img1:hover {
    box-shadow: 0 0 2px 2px rgba(32, 53, 238, 0.5);
}

@media (max-width:768px) {
    .img-container {
        float: left;
        width: 50%;
        position: relative;
        flex-wrap: nowrap;
     }
     .main{
        display: flex;
        text-align: center;
        border:10px solid white;
        width:190px;
        height:206px;
        margin : 40px auto;
        box-shadow: 0px 0px 25px black;
        overflow: hidden;
        flex-wrap: wrap;
       }
     .img1{
    
        border: 1px solid #ddd;
        border-radius: 4px;
        padding: 0px;
        width: 170px;
        height: 190px;
        margin: 2px;
        background-size: cover;
    
    }
}